#project {
  background-color: #262c4d;
  color: white;
  padding-top: 30px;
  font-weight: bold;
  font-size: 22px;
  text-align: center;
}
.pro-heading {
  font-size: 29px;
  font-family: "Kanit", sans-serif;
  font-weight: 400;
  font-style: bold;
}
.card {
  padding: 0px;
  margin: 40px;
  height: 18rem;
  border-radius: 15px;
  border: 0px;
  /* background-color: #262C4D; */
  background-color: #343d68;
  border-width: 2 px;
  border-image: linear-gradient(to right, blue, darkblue, #0d54be) 1;
  color: white;
}
.card-body {
  padding: 0px;
  height: 150px;
}
.anchor-btn {
  text-decoration: none;
  color: white;
  font-size: 15px;
  text-align: center;
  font-family: "Josefin Sans", sans-serif;
  font-optical-sizing: auto;
  font-size: 19px;
  font-style: normal;
}
.card-btn {
  color: #262c4d;
  background-color: #343d68;
  border-color: #6d78b7;
  border-radius: 10px;
  text-align: center;
  margin: 10px;
}
.card-btn:hover {
  border-color: #555555;
  border-radius: 10px;
  background-color: #262c4d;
}
.card-img-top {
  height: 200px;
  width: auto;
  filter: brightness(110%) contrast(110%);
}
.card-title {
  margin: 10px;
}
@media only screen and (max-width: 600px) {
  .card {
    margin-left: 50px;
  }
  .pro-heading {
    font-size: 22px;
  }
}
