@import url("https://fonts.googleapis.com/css2?family=Nunito:ital,wght@0,200..1000;1,200..1000&family=Titillium+Web:ital,wght@0,200;0,300;0,400;0,600;0,700;0,900;1,200;1,300;1,400;1,600;1,700&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Cantata+One&family=Hind:wght@300;400;500;600;700&family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Lora:ital,wght@0,400..700;1,400..700&family=Sora:wght@100..800&family=Zilla+Slab:ital,wght@0,300;0,400;0,500;0,600;0,700;1,300;1,400;1,500;1,600;1,700&display=swap");
body {
  height: 100%;
}
body {
  overflow-x: hidden;
  margin: 0;
  padding: 0;
  background: radial-gradient(
    ellipse at center,
    rgba(255, 254, 234, 1) 0%,
    rgba(255, 254, 234, 1) 35%,
    #b7e8eb 100%
  );
}
nav {
  font-family: "Sora", sans-serif;
  font-optical-sizing: auto;
  font-style: normal;
}
.nav-text {
  padding: 8px;
  font-size: 1.2rem;
  color: white;
}
.navbar-nav .nav-link:hover {
  color: rgba(227, 227, 227, 0.448);
}
.navbar-nav {
  margin-left: auto;
}
.nav-item {
  margin-left: 15px;
}
.navbar-nav .nav-link:visited {
  color: white;
}
.toggle-background {
  background-color: #262c4d;
  color: white;
}

@media only screen and (max-width: 600px) {
  * {
    overflow-x: hidden;
  }
  .nav-text {
    padding: 0px 0px;
    text-align: center;
    font-size: 0.9rem;
  }
  .navbar-nav .nav-link:visited {
    color: black;
    margin-bottom: 70px;
  }
  .navbar-brand {
    padding-left: 5px;
    font-size: 17px;
  }
  .nav-item {
    margin-bottom: 6px;
    margin-left: 0px;
    font-size: 17px;
  }
  .nav-text {
    font-size: 1.3rem;
    margin: 8px;
    color: white;
  }
  .navbar-toggler-icon {
    background-image: url(../Assets/toggler-image.png);
    /* margin-right: 20px; */
    background-color: #262c4d;
    color: #ffff;
  }

  .navbar {
    width: 100%;
  }
}
