/* resume */
@import url("https://fonts.googleapis.com/css2?family=Cantata+One&family=Josefin+Sans:ital,wght@0,100..700;1,100..700&family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");

.home {
  margin-top: 60px;
}

.avatar {
  width: 249px;
  height: 249px;
  flex-shrink: 0;
  margin-top: 70px;
  filter: brightness(100%) contrast(130%);
}

.home-head {
  font-family: "Sora", sans-serif;
  font-size: 40px;
  font-optical-sizing: auto;
  font-style: normal;
  letter-spacing: -2.5px;
  word-spacing: 15px;
}
.avathar-img {
  margin-top: 60px;
  width: 230px;
  height: 230px;
  border-radius: 230px;
  border-color: rgb(134, 213, 245);
  margin-left: -10px;
}
.home-about-content {
  font-family: "Cantata One", serif;
  font-size: 19px;
}
.content {
  color: #000;
  margin-top: 110px;
  /* text-align: justify; */
}
.cv-btn {
  width: 100px;
  background-color: #1b234a;
  color: white;
  font-family: "Josefin Sans", sans-serif;
  font-optical-sizing: auto;
  font-size: 19px;
  font-style: normal;
}
.head-span-blue{
  color: orangered;
  transition: 1s;
}
.head-span-blue:hover{
  color: black;
  transition: 1s;
}
@media only screen and (max-width: 600px) {
  .avatar {
    margin-top: 27px;
    width: 230px;
    height: 230px;
    border-radius: 230px;
    margin-left: -10px;
  }
  .cv-btn {
    margin-left: 150px;
  }
  .avathar-img {
    margin-top: 0px;
    margin-left: 5px;
    width: 210px;
    height: 210px;
    border-radius: 230px;
  }
  .content {
    color: #000;
    margin-top: 30px;
    font-size: 15px;
    /* word-spacing: 10px;  */
    padding-left: 30px;
    margin: 20px;
    /* text-align: justify; */
  }
  .home-head {
    font-size: 23px;
    /* padding-right: 10px; */
    text-align: center;
    margin-left: -15px;
  }
  .home-about-content {
    font-size: 16px;
    padding-right: 10px;
    margin-top: 25px;
    text-align: justify;
  }
}
